.header {
  width: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0);
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
}

.header.solid_header {
  background-color: #0c0b0b;
}

.header .logo_link svg {
  width: 98px;
  height: 15px;
}

@media screen and (min-width: 768px) {
  .header .logo_link svg {
    width: 202px;
    height: 31px;
  }
}

.header .wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header .wrapper .search_form {
  width: 108px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.header .wrapper .search_form :is(input:focus, input:valid) ~ div > .label {
  display: none;
}

.header .wrapper .search_form :is(input:focus, input:valid) ~ .button_label_wrap {
  left: 18.5px;
}

.header .wrapper .search_form .button_label_wrap {
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
  position: absolute;
}

.header .wrapper .search_form .button_label_wrap .search_button {
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.header .wrapper .search_form .button_label_wrap .search_button .search_icon {
  stroke: #fafafa;
  fill: rgba(0, 0, 0, 0);
  transition: stroke .35s ease-in-out;
}

.header .wrapper .search_form .button_label_wrap .label {
  text-transform: uppercase;
  cursor: text;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
}

.header .wrapper .search_form .input {
  width: 100%;
  color: #fafafa;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(250, 250, 250, .3);
  border-radius: 1000px;
  padding: 6px 18px 6px 40px;
  font-size: 12px;
  line-height: 1.17;
  transition: outline .35s ease-in-out, border-color .35s ease-in-out;
}

.header .wrapper .search_form .input:focus {
  border-color: #ed1d24;
  outline: none;
}

.header .wrapper .search_form .input:focus + .button_label_wrap svg {
  stroke: #ed1d24;
}

